<template>
  <div class="page">
    <breadcrumb/>
    <div class="page-content">
      <a-row :gutter="16">
        <a-col :span="16">
          <a-row class="toolbar">
            <a-button icon="plus" class="mr-1" @click="addFilterChain"></a-button>
            <a-button icon="reload" @click="handleRefresh"></a-button>
          </a-row>
          <a-spin :spinning="loading">
            <div class="filter-chain-container">
              <div v-for="chain in filterChains" :key="chain.id" class="filter-chain-box mb-1">
                <a-row>
                  <a-col :span="10">
                    <span><a-icon type="link"/> URL</span>
                    <a-input size="small" v-model="chain.url" v-if="showInput(chain.id)" class="edit-input"></a-input>
                    <b v-else>{{chain.url}}</b>
                  </a-col>
                  <a-col :span="10">
                    <span><a-icon type="filter"/> </span>
                    <a-input size="small" v-model="chain.filter" v-if="showInput(chain.id)" class="edit-input"></a-input>
                    <b v-else>{{chain.filter}}</b>
                  </a-col>
                  <a-col :span="4">
                    <a-button size="small" :icon="showInput(chain.id) ? 'check' : 'edit'" class="mr-1" @click="toggleEditChain(chain)"></a-button>
                    <a-popconfirm title="" trigger="click" placement="top" @confirm="handleDeleteFilterChain(chain.id)"
                                  :okText="$messages.operation.confirmText" :cancelText="$messages.operation.cancelText">
                      <a-button size="small" icon="delete"></a-button>
                    </a-popconfirm>
                  </a-col>
                </a-row>
              </div>
            </div>
          </a-spin>
        </a-col>
        <a-col :span="8">
          <a-alert type="info" showIcon>
            <template slot="message">
              <div></div>
              <div><b>anon</b>,</div>
              <div><b>authc</b></div>
              <div><b>authcBasic</b>httpBasic</div>
              <div><b>logout</b></div>
              <div><b>perms</b>/admins/user/**=perms["user:add:*,user:modify:*"]
                isPermitedAll()</div>
              <div><b>port</b>URL8081schemal://serverName:8081?queryString
                schmalhttphttpsserverNameHost8081PortqueryStringURL?</div>
              <div><b>rest</b>/admins/user/**=perms[user:method]methodpostgetdelete</div>
              <div><b>roles</b>,/admins/user/**=roles["admin,guest"]
                hasAllRoles()</div>
              <div><b>ssl</b>,URLhttps</div>
              <div><b>user</b>,</div>
              <a-divider/>
              <div></div>
              <div><b>userauthc</b>userShirorememberMe
                rememberMeuserauthc
              </div>
            </template>
          </a-alert>
        </a-col>
      </a-row>
    </div>

    <a-modal title=""
             :maskClosable="false"
             :visible="addVisible"
             @ok="handleAddFilterChain"
             @cancel="() => {this.addVisible = false}">
      <a-form>
        <a-form-item label="URL" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
          <a-input v-model="filterChain.url" placeholder="URL"></a-input>
        </a-form-item>
        <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
          <a-input v-model="filterChain.filter" placeholder=""></a-input>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script>
  import Breadcrumb from "../../../../components/Breadcrumb";
  export default {
    name: "FilterChain",
    components: {Breadcrumb},
    data() {
      return {
        filterChains: [
          {"id":"1055374084029657090","url":"/favicon.ico","filter":"anon","sortCode":1,"createdAt":"2018-10-25T16:22:44","createdBy":null,"updatedAt":"2018-10-25T16:22:44","updatedBy":null},
          {"id":"1055374084163874817","url":"/login","filter":"anon","sortCode":2,"createdAt":"2018-10-25T16:22:44","createdBy":null,"updatedAt":"2018-10-25T16:22:44","updatedBy":null},
          {"id":"1055374084163874818","url":"/css/**.css","filter":"anon","sortCode":3,"createdAt":"2018-10-25T16:22:44","createdBy":null,"updatedAt":"2018-10-25T16:22:44","updatedBy":null},
          {"id":"1055374084218400769","url":"/js/**.js","filter":"anon","sortCode":4,"createdAt":"2018-10-25T16:22:44","createdBy":null,"updatedAt":"2018-10-25T16:22:44","updatedBy":null},
          {"id":"1055374084218400770","url":"/img/**","filter":"anon","sortCode":5,"createdAt":"2018-10-25T16:22:44","createdBy":null,"updatedAt":"2018-10-25T16:22:44","updatedBy":null},
        ],
        loading: false,
        editMode: true,
        editId: '',
        editBtnLoading: false,
        addVisible: false,
        filterChain: {}
      }
    },
    created() {
      this.fetchData();
    },
    methods: {
      fetchData() {
        this.loading = true;
        this.$api.sys.getFilterChainList({orderByAsc: 'sort_code'}).then(res => {
          this.filterChains = res.data;
          this.loading = false;
        }).catch(() => {
          this.loading = false;
        });
      },
      addFilterChain() {
        this.filterChain = {};
        this.addVisible = true;
      },
      handleAddFilterChain() {
        this.$api.sys.addFilterChain(this.filterChain).then(() => {
          this.$message.success(this.$messages.successResult.create);
          this.addVisible = false;
          this.fetchData();
        });
      },
      handleDeleteFilterChain(chainId) {
        this.$api.sys.deleteFilterChain(chainId).then(() => {
          this.$message.success(this.$messages.successResult.delete);
          this.fetchData();
        });
      },
      handleRefresh() {
        this.fetchData();
      },
      toggleEditChain(chain) {
        if (this.showInput(chain.id)) {
          this.editBtnLoading = true;
          this.$api.sys.updateFilterChain(chain.id, chain).then(() => {
            this.$message.success(this.$messages.successResult.edit);
            this.editMode = false;
            this.editId = '';
            this.editBtnLoading = false;
          }).catch(() => {
            this.editMode = false;
            this.editId = '';
            this.editBtnLoading = false;
          });
        } else {
          this.editMode = true;
          this.editId = chain.id;
        }
      },
      showInput(chainId) {
        return this.editMode && this.editId === chainId;
      }
    }
  }
</script>

<style scoped>
  .filter-chain-container {
    height: calc(100vh - 292px);
    overflow-y: auto;
  }
  .toolbar {
    line-height: 3rem;
  }
  .filter-chain-box {
    padding: 8px;
    border: 1px solid #e8e8e8;
    border-radius: 4px;
  }
  .edit-input {
    width: calc(100% - 86px);
  }
</style>